@import url(https://fonts.googleapis.com/css2?family=Karla&family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.errorRow {
  color: var(--main-text);
  font-weight: 800;
}

.errorText {
  color: var(--error);
}

.login-container {
  width: 100vw;
  padding: 0;
}

.connect-to-yahoo a {
  border: 2px solid black;
  border-radius: 4px;
  padding: 10px;
  background-color: oldlace;
  color: black;
  font-weight: bold;
  text-decoration: none;
}

.connect-to-yahoo a:hover {
  background-color: var(--gradient1);
}

ul {
  display: flex;
  justify-content: space-around;
}

li.react-tabs__tab {
  padding: 10px 3vw 5px;
  position: relative;
  border: 1px solid var(--gradient1);
  border-radius: 8px 8px 0 0;
  border-bottom: none;
  bottom: 10px;
  cursor: pointer;
  background: whitesmoke;
}

.react-tabs__tab-panel {
  transition: background 0.2s ease;
  padding: 0 20px;
  margin-top: 0;
}

#react-tabs-1 > div {
  padding-top: 20px;
}

#react-tabs-3 > div {
  padding-top: 18px;
}

#react-tabs-5 > div {
  padding-top: 16px;
}

.react-tabs__tab:hover {
  background: var(--gradient1);
  transition: background 0.2s ease;
}

li.react-tabs__tab--selected:hover {
  cursor: auto;
}

li.react-tabs__tab--selected, .react-tabs__tab-panel {
  border: 1px solid transparent;
  background: var(--gradient1);
  transition: background 0.2s ease;
}

.navbar-tabs {
  padding: 20px 0 10px 0;
  font-size: 2rem;
  width: calc(100vw - 250px);
}

.react-tabs__tab--selected {
  font-weight: bold;
  position: fixed;
  /* top: 75px; */
}

.navbar-tabs > div {
  position: relative;
  width: 100%;
  top: -10px;
  /* overflow-y: scroll; */
}

#logout {
  position: fixed;
  top: 12px;
  right: 12px;
  z-index: 99;
}


.search-filter {
  width: 100%;
  margin: 0;
}

table {
  background: var(--table-bg);
  border-spacing: 0;
  text-align: left;
  border-radius: 4px;
  padding: 0; 
  font-size: 0.9rem;
  margin: 0;
  width: 100vw;
  max-width: 100%;
  padding-right: 22vw;
  -webkit-column-width: auto;
          column-width: auto;
  /* max-width: calc(100vw - 250px); */
}

.table thead th {
  vertical-align: top;
  font-size: 1.1rem;
  padding: 4px;
}

thead, tbody {
  max-width: 100%;
}

/* .tableWrap {
  display: block;
  max-width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
  border-bottom: 1px solid black;
} */

tr:nth-child(odd) {
  background-color: rgba(164, 189, 205, 0.7);
}

tr:nth-child(even) {
  background-color: rgba(200, 233, 255, 0.7);
}

tr:hover {
  background-color: rgb(166, 220, 250);
}

th, td {
  margin: 0;
  padding: 2px;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
}

td:last-child, th:last-child {
  border-right: 0;
}

table tr:last-child td {
  border-bottom: 0;
}

.blank-cell {
  background-color: transparent;
}

.draft-button-cell {
  width: 20px;
}

.draft-button-cell div {
  display: flex;
  justify-content: center;
}

.prospect {
  background: lightcyan;
  border: 1px solid lightgreen;
  border-radius: 100%;
  padding: 4px 7px;
}

#prospect-column {
  background: transparent;
  position: absolute;
  top: 0;
  left: 270px;
  border: none;
}

.prospect-column-hidden {
  width: 0;
  padding: 0 !important;
  border: none;
}

.post-title {
  padding: 0 !important;
  margin: 0 !important;
}

.post-title > div {
  vertical-align: center;
  cursor: pointer;
  width: 100%;
  height: 100%;
  padding: 20px;
}


.pagination {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-width: 550px;
  padding: 0 40px 0 0;
  margin: 0 0 20px 0;
}

.pagination-arrows {
  display: flex;
  color: black !important;
  cursor: pointer;
}

.pagination-arrows span {
  color: #000 !important;
}

.pagination li, .pagination select {
  height: 33px;
  width: 250px;
}

.pagination li.pagination-goto-page {
  display: flex;
  flex-direction: row;
  width: 200px;
  border-radius: 4px;
}

.page-link {
  display: flex;
  color: black;
}

.page-of-page {
  width: 70px;
}

.pagination-goto-page a {
  width: 200px;
}

.pagination-goto-page .page-link:first-child {
  border-radius: 4px 0 0 4px;
}

.pagination-goto-page .page-link:last-child {
  border-radius: 0 4px 4px  0;
}

.pagination li {
  font-size: 0.9rem;
  width: 130px;
}

.ReactModal__Content ReactModal__Content--after-open {
  min-width: 500px;
  height: 80vh;
  min-height: 400px; 
  /* margin-left: 25vw; */
}

#draft-player-modal {
  width: 50vw;
  height: 350px;
}

#forum-post-modal, #new-forum-post-modal{
  background-color: wheat;
  width: calc(90vw - 250px);
  margin: 0;
  height: 85vh;
}

.ReactModal__Overlay--after-open {
  background-color: #5e8b950a !important
}

.ReactModal__Content {
  width: 70vw;
  min-width: 400px;
  margin-left: 15vw;
  height: 500px;
  margin-top: 50px;
}

.close-button-wrapper {
  display: flex;
  justify-content: right;
}

.close-modal {
  display: flex;
  position: fixed;
}

.modal-title {
  font-size: 1.6rem;
  padding: 15px 35px 35px 35px;
  text-align: center;
  font-weight: 800;
}

.modal-forum-user {
  display: flex;
  margin: 10px 10px 0 40px;
  font-weight: 700;
}

.modal-player-info {
  margin: 20px auto;
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
}

.button-group {
  max-width: 500px;
  display: flex;
  justify-content: space-around;
  margin: 80px auto;
} 

.modal-forum-text {
  border: 1px solid gainsboro;
  border-radius: 4px;
  background: whitesmoke;
  text-align: left;
  font-size: 1.1rem;
  margin: 10px 25px 25px 25px;
  padding: 5px;
}

.modal-forum-text > p {
  margin: 5px;
  padding: 5px;
}

.modal-forum-date {
  font-style: italic;
  font-size: 0.7rem;
  font-weight: 400;
  padding: 4px 0 0 0;
}

.forum-reply {
  padding-left: 50px;
}

.new-forum-post-modal {

}

.new-forum-post-form {
  display: flex;
  flex-direction: column;
}

.new-forum-post-form > div {
  display: flex;
  padding: 10px 20px;
  justify-content: space-around;
  flex-direction: column;
}

.new-forum-post-form label {
  display: flex;
  font-weight: bold;
}

.new-forum-post-form input, .new-forum-post-form textarea {
  width: 90%;
}

.loading-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: transparent;
  transition: background 0.15s ease;
  height: 100vh;
  width: calc(100vw - 250px);
  color: whitesmoke;
  padding-bottom: 20vh;
  z-index: 99;
}

.loading-gif {
  display: flex;
  justify-content: center;
  align-items: center;
}


#chatbox {
  position: fixed;
  display: flex;
  justify-content: left;
  top: 45px;
  right: 0;
  margin: 0 0 0 10px;
  padding: 10px;
  border-left: 2px solid black;
  border-top: 2px solid black;
  border-radius: 4px 0 0 0;
  height: calc(100vh - 45px);
  width: 20vw;
  min-width: 250px;
  background-color: whitesmoke;
}

#chat-messages {
  position: fixed;
  display: flex;
  justify-content: left;
  text-align: left;
  top: 70px;
  right: 0;
  width: 19vw;
  min-width: 245px;
  height: calc(100vh - 110px);
  padding: 5px;
  margin: 2px;
  word-wrap: break-word;
  overflow: scroll;
}

#chat-title {
  position: relative;
  bottom: 40px;
  font-size: 1.1rem;
}

.user {
  color: blue;
  font-weight: bold;
  font-size: 0.6rem;
}

.message {
  word-break: break-all;
  word-break: break-word;
  font-size: 0.8rem;
}

#messageInput {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 18.5vw;
  min-width: 230px;
  padding: 5px;
}

#send-message-button {
  position: fixed;
  right: -200px;
  /* display: none; */
}

:root {
  --main-bg: #ffffff;
  --main-text:  #1b2733;
  --alt-text:  #e0e0e0;
  --gradient1: rgba(38, 38, 114, 0.3);
  --gradient2: rgba(56, 56, 141, 0);
  --gradient3: rgba(56, 100, 141, 0.3);
  --gradient4: rgba(56, 100, 141, 0);
  --main-dark: rgba(38, 38, 114, 0.8);
  --table-bg: white;

  --alt-bg: #637282;
  --h1-color: var(--alt-text);
  --header-bg: var(--main-dark);
  --alt-bg-opaque: rgba(38, 38, 114, 0.5);

  --header-text: #ffffff;
  --button: var(--main-dark);
  --eligible: #00ff99;
  --error: rgba(238, 11, 11, 0.651);
}

/* ðŸŽ¨ colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

main {
  display: flex;
  height: 100vh;
  overflow-y: scroll;
  max-height: 95%;
  background: #ffffff;
  background: var(--main-bg);
  background:          
            linear-gradient(336deg, rgba(38, 38, 114, 0.3), rgba(56, 56, 141, 0) 70.71%),
            linear-gradient(127deg, rgba(56, 100, 141, 0.3), rgba(56, 100, 141, 0) 70.71%);
  background:          
            linear-gradient(336deg, var(--gradient1), var(--gradient2) 70.71%),
            linear-gradient(127deg, var(--gradient3), var(--gradient4) 70.71%);         
  grid-template-columns: minmax(100%, 10%);
  text-align: center;
  font-family: 'Karla', 'Rubik', sans-serif;
  /* position: relative; */
  margin: 0;
  width: 80vw;
  /* padding-right: 22vw; */
  min-width: 500px;
  /* margin: 0 auto; */
  padding: 0;

  /* font-family: 'Rubik', sans-serif; */
}
