ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1 {
  margin: 50px auto;
  padding: 5px;
  background: rgba(38, 38, 114, 0.5);
  background: var(--alt-bg-opaque);
  max-width: 100%;
  color: #e0e0e0;
  color: var(--h1-color);
  font-size: 2rem;
}

h2 {
  background-color: transparent;
  border-radius: 8px 8px 0 0;
  font-size: 1.7rem;
  color: #ffffff;
  color: var(--header-text);
  margin: 0 auto;
  text-align: center;
  padding: 5px 5px;
}

h3 {
  color: #637282;
  color: var(--alt-bg);
  font-size: 1.6rem;
  margin: 30px 0 0;
  padding: 5px;
  width: auto;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.App {
  text-align: center;
}

button {
  border-radius: 4px;
  border: 1px solid rgba(38, 38, 114, 0.8);
  border: 1px solid var(--button);
  color: white;
  background: rgba(38, 38, 114, 0.3);
  background: var(--gradient1);
}

.button-large {
  font-size: 1.4rem;
  padding: 7px 15px;
}

button:hover {
  background: rgba(56, 100, 141, 0.3);
  background: var(--gradient3);
}

.hidden {
  display: none;
}

.margin-15 {
  margin: 15px;
}

